<template>
  <div :class="className" :style="{ height: height, width: width }" />
</template>

<script>
import * as echarts from "echarts";
require("echarts/theme/macarons"); // echarts theme
import resize from "./mixins/resize";

let now = new Date(1997, 9, 3);
// let oneDay = 24 * 3600 * 1000;
let value = Math.random() * 1000;

export default {
  mixins: [resize],
  props: {
    className: {
      type: String,
      default: "chart",
    },
    width: {
      type: String,
      default: "100%",
    },
    height: {
      type: String,
      default: "550px",
    },
    autoResize: {
      type: Boolean,
      default: true,
    },
    testData11: {
      type: Array,
      default: [
        ["2022-11-09 17:54:43", 11],
        ["2022-11-09 17:56:43", 13],
      ],
    },
    temperData: {
      type: Array,
      default: [
        ["2022-11-09 17:54:43", 11],
        ["2022-11-09 17:56:43", 13],
      ],
    },
    // oxygenData: String,
    // temprature: String
    // chartData: {
    //   type: Object,
    //   required: true,
    // },
  },
  data() {
    return {
      chart: null,
      testData: [],
    };
  },
  computed:{
    livedOxygenLimit(){
      return this.$store.state.oxygenLimit;
    }
  },
  watch: {
    testData11: {
      handler(val) {
        this.chart.setOption({
          series: [
            {
              name: "溶氧",
              data: val,
            },
          ],
        });
      },
    },
    temperData: {
      handler(val) {
        this.chart.setOption({
          series: [
            {
              name: "温度",
              data: val,
            },
          ],
        });
      },
    },
    livedOxygenLimit: {
      handler(val) {
        // console.log('oxygenLimit:', val)
        this.setOptions()
      },
    },
  },
  mounted() {
    // this.height = `${window.screen.height/2}px`;
    this.$nextTick(() => {
      this.initChart();
      // console.log('mounted:',this.$store.state.oxygenLimit)
    });
  },
  beforeDestroy() {
    if (!this.chart) {
      return;
    }
    this.chart.dispose();
    this.chart = null;
    clearInterval(this.dataTimer);
  },
  methods: {
    initChart() {
      this.chart = echarts.init(this.$el, "macarons");
      this.chart.showLoading();
      this.setOptions();
      this.chart.hideLoading();
    },
    setOptions() {
      this.chart.setOption({
        animation: false,
        xAxis: [
          {
            type: "time",
            //data: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"],
            boundaryGap: false,
            axisLabel: {
              align: "center",
              formatter: function (value, index) {
                // 格式化成时分秒
                // console.log('index:', index)
                let date = new Date(value);
                // let nowHours = date.getHours();
                let nowMin = date.getMinutes().toString().padStart(2, "0");
                let nowSeconds = date.getSeconds().toString().padStart(2, "0");
                var texts = [nowMin, nowSeconds];
                return texts.join(":");
                // return nowSeconds.toString();
              },
            },
            // splitLine: {
            //   show: false,
            // },
            axisLine: { onZero: true },
          },
          {
            gridIndex: 1,
            type: "time",
            boundaryGap: false,
            axisLine: { onZero: true },
            position: "bottom",
          },
        ],
        grid: [
          // left: 10,
          // right: 10,
          // bottom: 20,
          // top: 30,
          // containLabel: true,
          {
            left: 30,
            right: 20,
            height: "55%",
          },
          {
            left: 30,
            right: 20,
            top: "70%",
            bottom: 20,
            height: "30%",
          },
        ],
        tooltip: {
          trigger: "axis",
          backgroundColor: "#D38359",
          formatter: function (params) {
            // console.log('params:', params);
            let oxygenobj = params[0].axisIndex === 0 ? params[0] : params[1];
            let temperobj = params[0].axisIndex === 1 ? params[0] : params[1];
            return (
              oxygenobj.data[0] +
              "<br>" +
              oxygenobj.marker +
              oxygenobj.seriesName +
              "&emsp;&emsp;" +
              oxygenobj.data[1] +
              "<br>" +
              temperobj.marker +
              temperobj.seriesName +
              "&emsp;&emsp;" +
              temperobj.data[1]
            );
          },
          axisPointer: {
            // type: "cross",
            animation: false,
          },
          // padding: [5, 10],
        },
        yAxis: [
          {
            name: "溶氧",
            type: "value",
            boundaryGap: [0, "100%"],
            axisTick: {
              show: false,
            },
          },
          {
            name: "温度",
            type: "value",
            gridIndex: 1,
            // inverse: true,
          },
        ],
        axisPointer: {
          link: [
            {
              xAxisIndex: "all",
            },
          ],
        },
        legend: {
          // data: ["expected", "actual"],
          data: ["溶氧", "温度"],
        },
        dataZoom: [
          //   type: "inside",
          //   start: 0,
          //   end: 20,
          {
            show: true,
            realtime: true,
            // start: 30,
            // end: 70,
            xAxisIndex: [0, 1],
          },
          {
            type: "inside",
            realtime: true,
            // start: 30,
            // end: 70,
            xAxisIndex: [0, 1],
          },
        ],
        toolbox: {
          feature: {
            dataZoom: {
              yAxisIndex: "none",
            },
            restore: {},
            saveAsImage: {},
          },
        },
        series: [
          {
            name: "溶氧",
            markLine: {
              //最大值和最小值
              data: [
                {
                  name: "阈值",
                  yAxis: this.livedOxygenLimit, //阈值设置
                  label: {
                    show: "true",
                    position: "middle",
                    formatter: `溶氧红线：${this.livedOxygenLimit}`,
                    color: "inherit",
                    fontSize: 20,
                    fontWeight: "bold",
                  },
                  lineStyle: {
                    normal: {
                      width: 2,
                      color: "#FF5D1D",
                    },
                  },
                },
              ],
            },
            itemStyle: {
              normal: {
                color: "#64EDE0",
                lineStyle: {
                  color: "#FF005A",
                  width: 2,
                },
              },
            },
            smooth: true,
            areaStyle: {},
            type: "line",
            data: this.testData11,
            // animationDuration: 2800,
            // animationEasing: "cubicInOut",
          },
          {
            name: "温度",
            type: "line",
            xAxisIndex: 1,
            yAxisIndex: 1,
            data: this.temperData,
          },
        ],
      });
    },
    createData() {
      let that = this;
      that.dataTimer = setInterval(() => {
        that.testData.shift();
        that.testData.push(that.randomData());
        // console.log('testdata:', that.testData)
        that.chart.setOption({
          series: [
            {
              data: that.testData,
            },
          ],
        });
      }, 1000);
    },

    randomData() {
      // now = new Date(+now + oneDay);
      now = new Date();
      value = value + Math.random() * 21 - 10;
      // console.log('value:', value)
      // this.oxygenData = value;
      this.$emit("temperChange", `${Math.round(value)} mg/L`);
      return {
        name: now.toString(),
        value: [
          [now.getFullYear(), now.getMonth(), now.getDate()].join("/") +
            " " +
            now.getHours() +
            ":" +
            now.getMinutes().toString().padStart(2, "0") +
            ":" +
            now.getSeconds().toString().padStart(2, "0"),
          // [now.getHours(), now.getMinutes(), now.getSeconds()].join(":"),
          // now.toString(),
          Math.round(value),
        ],
      };
    },
  },
};
</script>
