<template>
  <div class="login-container">
    <van-nav-bar title="首页--实时数据"> </van-nav-bar>
    <van-cell-group inset>
      <!-- <van-cell title="溶氧：" center size="small" /> -->
      <van-field
        v-model="oxygenData"
        label="溶氧："
        placeholder="0"
        label-align="right"
        input-align="center"
      />
      <!-- <h1>{{temprature}}</h1> -->
      <van-field
        v-model="temprature"
        label="温度："
        placeholder="0"
        label-align="right"
        input-align="center"
      />
      <!-- <van-cell title="温度：" :value="temprature" label="描述信息" /> -->
    </van-cell-group>
    <LineChart
      v-on:temperChange="updateTemp"
      :testData11="testresponse"
      :temperData="tempratureData"
      :height="calculateHeight"
    />
    <!-- <van-tabbar v-model="active">
      <van-tabbar-item icon="home-o">首页</van-tabbar-item>
      <van-tabbar-item icon="chart-trending-o" to:>历史数据</van-tabbar-item>
      <van-tabbar-item icon="setting-o">控制</van-tabbar-item>
      <van-tabbar-item icon="manager-o">我的</van-tabbar-item>
    </van-tabbar> -->
  </div>
</template>

<script>
import { Locale } from "vant";
import LineChart from "./components/LineChart";

export default {
  name: "HomePage",
  components: {
    LineChart,
  },
  data() {
    return {
      ischecked: false,
      active: 0,
      oxygenData: "",
      temprature: "",
      testresponse: [],
      tempratureData: []
    };
  },
  computed: {
    calculateHeight: function () {
      let chartheight = window.screen.height - 46 - 48 - 50 - 40;
      return `${chartheight}px`;
    },
  },
  watch: {
    $route: {
      handler: function (route) {
        this.redirect = route.query && route.query.redirect;
      },
      immediate: true,
    },
  },
  mounted() {
    this.openWebSocket();
  },
  methods: {
    showPwd() {
      if (this.passwordType === "password") {
        this.passwordType = "";
      } else {
        this.passwordType = "password";
      }
      this.$nextTick(() => {
        this.$refs.password.focus();
      });
    },
    updateTemp(e) {
      this.temprature = e;
    },
    updateOxygen(e) {
      this.oxygenData = e;
    },
    openWebSocket() {
      let that = this;
      let base_url = location.host;
      // let websocketUrl = `ws://${base_url}/ws`
      let websocketUrl = "ws://127.0.0.1:8002/ws";
      console.log("websocketUrl:", websocketUrl);
      if ("WebSocket" in window) {
        console.log("您的浏览器支持WebSocket");
      } else {
        console.log("您的浏览器不支持WebSocket");
      }
      // create an websocket instance
      const ws = new WebSocket(websocketUrl);

      ws.onopen = function (e) {
        // heartCheck.reset().start();      //心跳检测重置
        console.log(new Date());
        console.log("websocket 已连接");
        console.log(websocketUrl);
      };
      ws.onerror = function (e) {
        console.log(e);
      };
      ws.onclose = function (e) {
        console.log(new Date());
        console.log("websocket 断开连接！");
      };
      ws.onmessage = function (event) {
        console.log("event_log:", event);
        console.log(event.data);
        let data = event.data;
        let datajson = JSON.parse(data);
        that.oxygenData = `${datajson.oxygen} mg/L`;
        that.temprature = `${datajson.temper} ℃`;
        let dateTime = datajson.date_time.replace(/-/g, "/")
        while (that.testresponse.length > 10) {
          that.testresponse.shift();
          that.tempratureData.shift();
        }
        // console.log('datetime:', dateTime)
        that.testresponse.push([dateTime, datajson.oxygen]);
        that.tempratureData.push([dateTime, datajson.temper])
        //心跳保活
        // heartCheck.reset().start();      //拿到任何消息都说明当前连接是正常的
        // if(event.data!="pong"){
        //   let data = event.data;
        //   console.log(data)
        //   console.log(typeof(data))
        //   let datajson = JSON.parse(data)
        //   console.log(datajson)
        //   console.log(typeof(datajson))
        //   that.testresponse.push([datajson.date_time,datajson.oxygen])
        // }
      };
      window.onbeforeunload = function () {
        ws.close();
      };

      //心跳检测
      // var heartCheck = {
      //     timeout: 1000,        //1分钟发一次心跳
      //     timeoutObj: null,
      //     serverTimeoutObj: null,
      //     reset: function(){
      //         clearTimeout(this.timeoutObj);
      //         clearTimeout(this.serverTimeoutObj);
      //         return this;
      //     },
      //     start: function(){
      //         var self = this;
      //         this.timeoutObj = setTimeout(function(){
      //             //这里发送一个心跳，后端收到后，返回一个心跳消息，
      //             //onmessage拿到返回的心跳就说明连接正常
      //             ws.send("ping");
      //             console.log("ping!")
      //             self.serverTimeoutObj = setTimeout(function(){//如果超过一定时间还没重置，说明后端主动断开了
      //                 ws.close();     //如果onclose会执行reconnect，我们执行ws.close()就行了.如果直接执行reconnect 会触发onclose导致重连两次
      //             }, self.timeout)
      //         }, this.timeout)
      //     }
      // }
    },
  },
};
</script>

<style lang="scss">
/* 修复input 背景不协调 和光标变色 */
/* Detail see https://github.com/PanJiaChen/vue-element-admin/pull/927 */

$bg: #283443;
$light_gray: #fff;
$cursor: #fff;

@supports (-webkit-mask: none) and (not (cater-color: $cursor)) {
  .login-container .el-input input {
    color: $cursor;
  }
}

/* reset element-ui css */
.login-container {
  .el-input {
    display: inline-block;
    height: 47px;
    width: 85%;

    input {
      background: transparent;
      border: 0px;
      -webkit-appearance: none;
      border-radius: 0px;
      padding: 12px 5px 12px 15px;
      color: $light_gray;
      height: 47px;
      caret-color: $cursor;

      &:-webkit-autofill {
        box-shadow: 0 0 0px 1000px $bg inset !important;
        -webkit-text-fill-color: $cursor !important;
      }
    }
  }

  .el-form-item {
    border: 1px solid rgba(255, 255, 255, 0.1);
    background: rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    color: #454545;
  }
}
</style>

<style lang="scss" scoped>
$bg: #2d3a4b;
$dark_gray: #889aa4;
$light_gray: #eee;

.login-container {
  min-height: 100%;
  width: 100%;
  background-color: $bg;
  overflow: hidden;

  .login-form {
    position: relative;
    width: 520px;
    max-width: 100%;
    padding: 160px 35px 0;
    margin: 0 auto;
    overflow: hidden;
  }

  .tips {
    font-size: 14px;
    color: #fff;
    margin-bottom: 10px;

    span {
      &:first-of-type {
        margin-right: 16px;
      }
    }
  }

  .svg-container {
    padding: 6px 5px 6px 15px;
    color: $dark_gray;
    vertical-align: middle;
    width: 30px;
    display: inline-block;
  }

  .title-container {
    position: relative;

    .title {
      font-size: 26px;
      color: $light_gray;
      margin: 0px auto 40px auto;
      text-align: center;
      font-weight: bold;
    }
  }

  .show-pwd {
    position: absolute;
    right: 10px;
    top: 7px;
    font-size: 16px;
    color: $dark_gray;
    cursor: pointer;
    user-select: none;
  }
}
</style>
